.message {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-wrap: wrap;
    margin-bottom: 47px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    flex-wrap: wrap;

}

.imageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
    margin-left: 12px;
}

.text {
    position: relative;
    padding: 8px 13px 7px 13px;
    background-color: #0066CC;
    color: #FFFFFF;
    border-radius: 20px 0 0 20px;
}

.text:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: -20px;
    border-left: 13px solid transparent;
    border-right: 13px solid transparent;
    border-bottom: 13px solid #0066CC; /* Цвет треугольника */
    transform: translateX(-50%);
}

.name {
    font-weight: 600;
    font-size: 18px;
    text-align: right;
}

.messageText {
    font-weight: 400;
    font-size: 16px;
}

.time {
    width: 100%;
    font-weight: 600;
    font-size: 10px;
    color: #000;
    text-align: right;
    padding-right: 11px;
}