.button {
    padding: 5px 24px;
    border: none;
    border-radius: 5px;
    background-color: #0066CC;
    color: #fff;

}

.disabled {
    background-color: #004D99;
    color: #002E5C;

}

.secondary {
    color: #0066CC;
    border: 1px solid #0066CC;
    background-color: #fff;
}

.default {
    color: #fff;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    opacity: 0.6;
}

