.sendForm {
margin-top: 73px;
    display: flex;
    flex-wrap: wrap;
}

.textarea {
max-width: 976px;
    width: 100%;
    padding: 12px 24px 11px 24px;
    border-radius: 20px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    background: #f5f7fb;
    margin-right: 44px;
    border: none;
}

.button {
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    padding: 10px 38px;
    border-radius: 20px;
    background-color: #0066CC;
    border: none;
}

.button:hover {
    background-color: #F5F7FB;
    color: #0066CC;
}
.button:active {
opacity: 0.6;
}
