.sidebar {
    display: none;
}

.sidebar.open {
    display: flex;
    flex-direction: column;
    max-width: 400px;
    height: 100vh;
    position: absolute;
    width: 317px;
    background-color: #fff;
    z-index: 1;
    padding: 20px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
}

.background {
}

.nav {
    display: flex;
    flex-direction: column;
    font-size: 22px;
    font-weight: 600;
    margin-top: 40px;
    gap: 45px;
}

.nav a {
    color: #000000;
    text-decoration: none;

}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
    text-decoration: underline;

}
.close {
    background: none;
    border: none;
    margin-left: auto;
}

