.friendMessage {
    display: flex;
    flex-direction: row;
    margin-bottom: 47px;
    flex-wrap: wrap;
}

.friendImageAndText {
    display: flex;

    flex-wrap: wrap;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    object-fit: cover;
    margin-right: 12px;
}

.friendText {
    position: relative;
    padding: 8px 13px 7px 13px;
    background-color: #F5F7FB;
    color: #000;
    border-radius: 0 20px 20px 0;
}

.friendText:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: -10px;
    border-top: 13px solid transparent;
    border-right: 13px solid #F5F7FB;
}

.friendName {
    font-weight: 600;
    font-size: 18px;
    text-align: right;
}

.friendMessageText {
    font-weight: 400;
    font-size: 16px;
}

.friendTime {
    width: 100%;
    font-weight: 600;
    font-size: 10px;
    color: #000;
    text-align: left;
    padding-left: 11px;
}
